header {
  height: 100vh;
  padding-top: 6rem;
  overflow: hidden;
}

.header__container {
  text-align: center;
  position: relative;
  height: 100%;
}
.specialFont {
  font-family: "Titillium Web", sans-serif;
}

.the_couples {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 2rem;
}

.specialFont_date {
  font-size: 2rem;
}

.bg_theme {
  background-image: url(../../assets/bg2.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

/**** CTA ****/
.cta {
  margin-top: 2.5rem;
  display: flex;
  gap: 1.2rem;
  justify-content: center;
}

/***** Header Socials ****/
.header__socials {
  display: flex;
  flex-direction: column;
  gap: 0.8rem;
  align-items: center;
  position: absolute;
  left: 0;
  bottom: 3rem;
}

.header__socials::after {
  content: "";
  width: 1px;
  height: 2rem;
  background: var(--color-primary);
}

/*** IMAGE *******/
.me {
  /* background: linear-gradient(var(--color-primary), transparent); */
  width: 22rem;
  height: 30rem;
  position: absolute;
  left: calc(50% - 11rem);
  margin-top: 4rem;
  bottom: 3rem;
  border-radius: 12rem 12rem 0 0;
  overflow: hidden;
  padding: 5rem 1.5rem 1.5rem 1.5rem;
}

/***** SCROLL DOWN ******/
.scroll_down {
  position: absolute;
  right: -2.3rem;
  bottom: 5rem;
  transform: rotate(90deg);
  font-weight: 300;
  font-size: 0.9rem;
}

/********** MEDIA QUERIES (Medium Devices) *********/
@media screen and (max-width: 1024px) {
  header {
    height: 68vh;
  }
}

/********** MEDIA QUERIES (Medium Devices) *********/
@media screen and (max-width: 600px) {
  header {
    height: 100vh;
  }
  .header__socials,
  .scroll_down {
    display: none;
  }
  .the_couples {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
}
